("En la literatura, los estereotipos son utilizados como herramientas que permiten a los autores conectar con sus lectores. Por ejemplo, un escritor puede describir a un personaje como \"el típico chico que siempre llega tarde\", y esto ayuda a que el lector entienda rápidamente, el tipo de persona que es, sin necesidad de una explicación larga.")])])],1)]),_vm._m(23),_c('div',{staticClass:"bg-full-width border-top color-primario"},[_c('div',{staticClass:"p-4 p-md-5"},[_c('h2',[_vm._v("MATERIAL COMPLEMENTARIO")]),_c('div',{staticClass:"row material-complem"},[_c('div',{staticClass:"col-12 col-md-6 col-lg-7"},[_c('p',[_vm._v("Los invitamos a explorar el material complementario de este curso, en esta sección encontrará recursos que le permitirán profundizar y enriquecer su aprendizaje en los temas tratados en esta unidad.")]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3",style:({"max-width":"16px"}),attrs:{"src":require("@/assets/componentes/book.svg")}}),_c('a',{attrs:{"href":"https://repository.unilibre.edu.co/bitstream/handle/10901/28316/ARTICULO%20-%20%20ESPECIALIZACION%20UNILIBRE%20-%20%20Racismo%20y%20Discriminacion%20en%20Colombia........%20AVANCE...pdf?sequence=1","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("Cifuentes, R.D.G. (2023). Racismo y discriminación en Colombia; una forma de exclusión social, laboral, política y económica a poblaciones étnicas.")])]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3",style:({"max-width":"16px"}),attrs:{"src":require("@/assets/componentes/link.svg")}}),_c('a',{attrs:{"href":"https://psicologiaynente.com/social/tipos-exclusion-social","target":"_blank","rel":"noopener noreferrer"}},[_vm._v("Hernández, L. M.C. (2020). Los 9 tipos de exclusión social, y cómo afectan a la ciudadanía. pymOrganization.")])]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3",style:({"max-width":"16px"}),attrs:{"src":require("@/assets/componentes/link.svg")}}),_c('a',{attrs:{"href":"h